<template>
  <div>
    <BaseBox>
      <h2>Gib das Wort ein, welches du gehört hast</h2>
      <input
        id="word-input"
        v-model="wordInput"
        type="text"
        @keydown.enter="nextWord"
      />
      <div>Wort {{ wordCounter }}/{{ wordAmount }}</div>
      <div>{{ countdown }}</div>
    </BaseBox>
    <div class="buttons-wrapper">
      <BaseButton @btnClicked="playAudio">Nochmal abspielen</BaseButton>
      <BaseButton @btnClicked="nextWord">Enter</BaseButton>
    </div>
    <audio v-for="index in wordAmount" :key="index" :id="`myAudio-${index}`">
      <source :src="`${host}/audio/${token}/${index - 1}`" type="audio/wav" />
      Your browser does not support the audio element.
    </audio>
  </div>
</template>

<script>
import BaseBox from "@/components/base/BaseBox.vue";
import BaseButton from "@/components/base/BaseButton.vue";
import axios from "axios";
import Store from "@/store";

export default {
  name: "PlayGame",
  components: {
    BaseBox,
    BaseButton,
  },
  data() {
    return {
      wordCounter: 1,
      countdown: 10,
      interval: null,
      wordInput: "",
      wordOB: [],
      host: process.env.VUE_APP_HOST,
    };
  },
  methods: {
    nextWord() {
      this.wordOB.push(this.wordInput);
      this.wordInput = "";
      clearInterval(this.interval);
      this.wordCounter++;
      this.countdown = 10;
      this.startCounter();
      this.playAudio();
    },
    startCounter() {
      this.interval = setInterval(() => {
        if (this.countdown > 1) {
          this.countdown--;
        } else {
          this.nextWord();
        }
      }, 1000);
    },
    playAudio() {
      if (this.wordCounter <= this.wordAmount) {
        document.querySelector(`#myAudio-${this.wordCounter}`).play();
      } else {
        // send array to backend
        axios({
          /* `${
            window.location.protocol + "//" + window.location.host
          }/result` */
          url: `${process.env.VUE_APP_HOST}/result/${this.token}`,
          method: "post",
          data: this.wordOB,
        }).then(function (response) {
          Store.mutations.setResult(response.data);
        });
        this.$emit("changeComponent", "scoreGame");
        return;
      }
    },
  },
  mounted() {
    this.startCounter();
    document.querySelector("#word-input").focus();
    this.playAudio();
  },
  computed: {
    token() {
      return localStorage.getItem("token");
    },
    wordAmount() {
      return Store.state.wordAmount;
    },
  },
  beforeUnmount() {
    clearInterval(this.interval);
    this.wordCounter = 0;
  },
};
</script>

<style scoped>
.buttons-wrapper {
  display: flex;
}
.buttons-wrapper > .button-wrapper {
  margin: 1rem 1rem;
}
</style>
