<template>
  <div class="button-wrapper">
    <button type="button" class="button" @click="emitBtnClick">
      <slot></slot>
    </button>
  </div>
</template>

<script>
export default {
  name: "BaseButton",
  methods: {
    emitBtnClick() {
      this.$emit("btnClicked");
    },
  },
};
</script>

<style scoped>
.button-wrapper {
  margin: 1rem 0;
  width: 100%;
}
.button {
  width: 100%;
  min-width: 300px;
  height: 3rem;
  border-radius: 25px;
  background-color: #cb99c9;
  color: #fff;
  font-weight: 600;
}
</style>
