<template>
  <div>
    <BaseBox>
      <h2>Dein Ergebnis:</h2>
      <div class="container">
        <div class="row">
          <div class="col-sm">
            <div
              v-for="(userWord, correctWord, index) in Object.fromEntries(
                Object.entries(result).slice(0, 5)
              )"
              :key="index"
            >
              <div
                :class="{
                  correct: userWord === correctWord,
                  incorret: userWord !== correctWord,
                }"
              >
                {{ userWord }}
              </div>
              <div v-if="userWord !== correctWord && userWord !== ''">
                ({{ correctWord }})
              </div>
            </div>
          </div>
          <div class="col-sm">
            <div
              v-for="(userWord, correctWord, index) in Object.fromEntries(
                Object.entries(result).slice(5, 10)
              )"
              :key="index"
            >
              <div
                :class="{
                  correct: userWord === correctWord,
                  incorret: userWord !== correctWord,
                }"
              >
                {{ userWord }}
              </div>
              <div v-if="userWord !== correctWord && userWord !== ''">
                ({{ correctWord }})
              </div>
            </div>
          </div>
        </div>
      </div>
    </BaseBox>
    <div class="buttons-wrapper">
      <BaseButton @btnClicked="changeComponent('mainMenu')"
        >Zur Startseite</BaseButton
      >
      <BaseButton @btnClicked="changeComponent('newGame')"
        >Neues Spiel</BaseButton
      >
    </div>
  </div>
</template>

<script>
import BaseBox from "@/components/base/BaseBox.vue";
import BaseButton from "@/components/base/BaseButton.vue";
import Store from "@/store";

import Score from "@/assets/score.js";

export default {
  name: "ScoreGame",
  components: {
    BaseBox,
    BaseButton,
  },
  data() {
    return {
      score: Score,
    };
  },
  methods: {
    changeComponent(componentName) {
      this.$emit("changeComponent", componentName);
    },
  },
  computed: {
    result() {
      return Store.state.result;
    },
  },
};
</script>

<style>
.buttons-wrapper {
  display: flex;
}
.buttons-wrapper > .button-wrapper {
  margin: 1rem 1rem;
}
.correct {
  color: green;
}
.incorret {
  color: red;
}
</style>
