<template>
  <div>
    <BaseBox>
      <p v-if="userNameStore">Hallo {{ userNameStore }}</p>
      <div v-else>
        <h2>Gib deinen Namen ein</h2>
        <input v-model="userName" type="text" />
        <p class="error-text" v-if="nameNotAvailable">
          Dieser Name ist aktuell in Verwendung...
        </p>
      </div>
    </BaseBox>
    <BaseButton v-if="userNameStore" @btnClicked="startGame('playGame')"
      >Spiel starten</BaseButton
    >
    <BaseButton v-else @btnClicked="setUserName">Namen speichern</BaseButton>
    <BaseButton @btnClicked="changeComponent('mainMenu')"
      >Zur Startseite</BaseButton
    >
  </div>
</template>

<script>
import BaseBox from "@/components/base/BaseBox.vue";
import BaseButton from "@/components/base/BaseButton.vue";
import axios from "axios";

import Store from "@/store";

export default {
  name: "NewGame",
  components: {
    BaseBox,
    BaseButton,
  },
  data() {
    return {
      userName: "",
      nameNotAvailable: false,
      validToken: false,
    };
  },
  async beforeMount() {
    await this.checkToken();
  },
  methods: {
    setUserName() {
      /* axios
        .get(`${window.location.protocol + "//" + window.location.host}/download`)
        .then((res) => console.log(res)); */
      // call api set username, get token back
      axios
        .get(`${process.env.VUE_APP_HOST}/user/${this.userName}`)
        .then((res) => {
          console.log(res.data);
          this.nameNotAvailable = false;
          localStorage.setItem("token", res.data);
          Store.mutations.setUserName(this.userName);
        })
        .catch(() => {
          this.nameNotAvailable = true;
        });
    },
    async startGame(componentName) {
      if (await this.checkToken()) {
        await this.setNewWords(localStorage.getItem("token"));
        console.log("change comp");
        this.$emit("changeComponent", componentName);
      }
    },
    changeComponent(componentName) {
      this.$emit("changeComponent", componentName);
    },
    async setNewWords(token) {
      // setup words for this username/token
      await axios.get(`${process.env.VUE_APP_HOST}/game/${token}`).then((res) => {
        console.log(res);
        Store.mutations.setWordAmount(parseInt(res.data));
      });
    },
    async checkToken() {
      const token = localStorage.getItem("token");
      let validToken = false;
      if (token) {
        await axios
          .get(`${process.env.VUE_APP_HOST}/validate_token/${token}`)
          .then((res) => {
            // this.validToken = res.data;
            Store.mutations.setUserName(res.data || "");
            if (!res.data) localStorage.removeItem("token");
            /* if (!res.data) {
          localStorage.removeItem("token");
        } */
            validToken = !!res.data;
          });
      }
      return validToken;
    },
  },
  computed: {
    userNameStore() {
      return Store.state.userName;
    },
  },
};
</script>

<style scoped>
.error-text {
  color: red;
}
</style>
