<template>
  <div>
    <BaseBox>
      <ul v-if="users.length">
        <li v-for="(user, index) in users" :key="index">
          {{ user }}
        </li>
      </ul>
      <div v-else>Aktuell ist keiner online</div>
    </BaseBox>
    <BaseButton @btnClicked="changeComponent('mainMenu')">Zurück</BaseButton>
  </div>
</template>

<script>
import BaseBox from "@/components/base/BaseBox.vue";
import BaseButton from "@/components/base/BaseButton.vue";
import axios from "axios";

export default {
  name: "ActiveUsers",
  components: {
    BaseBox,
    BaseButton,
  },
  data() {
    return {
      users: [],
    };
  },
  methods: {
    changeComponent(name) {
      this.$emit("changeComponent", name);
    },
  },
  beforeCreate() {
    axios.get(`${process.env.VUE_APP_HOST}/users`).then((res) => {
      console.log(res);
      this.users = res.data;
    });
  },
};
</script>

